import React from 'react';
import ActBase from '../../components/ActBase';
import jobColours from '../../util/colours'

const styles = {
  combatantContainer: {
    float: 'left', padding: '0 15px', textAlign: 'center', color: '#FFFFFF', fontWeight: 'bold'
  }
}

export default class Pixel extends React.Component {
  renderCombatant = ([k, v]) => {
    const upperJob = (v.Job || '').toUpperCase();
    const jobColour = jobColours(upperJob);
    return (
      <div style={{...styles.combatantContainer, textShadow: `0 0 5px ${jobColour}`}} key={v.name}>
        <div style={{height: 60, width: 52, backgroundImage: `url('/static/pixel/${upperJob}.png')`, backgroundPosition: 'center', backgroundRepeat: 'no-repeat', margin: '0 auto', animationDuration: '2s', animationIterationCount: 'infinite', animationName: 'bounce-2', animationTimingFunction: 'ease'}} />
        <div style={{fontSize: '0.9em'}}>{v.name} ({(v.Job || '').toUpperCase()})</div>
        <div>{v.encdps}</div>
      </div>
    );
  }
  render () {
    return (
      <ActBase>
      {({Combatant}) => (
        <>
          <style global jsx>{`
            body {
              font-family: monospace;
            }
            @keyframes bounce-2 {
              0%   { transform: translateY(0); }
              5%   { transform: translateY(0); }
              50%  { transform: translateY(-2px); }
              95%   { transform: translateY(0); }
              100% { transform: translateY(0); }
            }
          `}</style>
          {Combatant ? Object.entries(Combatant).map(this.renderCombatant) : null}
        </>
      )}
      </ActBase>
    );
  }
}

